import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'adStatusTooltip',
    standalone: false
})
export class AdStatusTooltipPipe implements PipeTransform {
	transform(status: string, getMessage: (status: string) => string): string | null {
		const statuses = ['sold', 'property-occupied', 'sale-agreed', 'expired'];
		return statuses.includes(status) ? getMessage(status) : null;
	}
}
