import { isPlatformBrowser } from '@angular/common';
import { Inject, Injectable, PLATFORM_ID, Renderer2, RendererFactory2 } from '@angular/core';
import { AmplitudeEvent } from '@core/enums/amplitude.enum';
import { environment } from '@environments/environment';

interface SendEnquiryAmplitudeEvent {
	user_logged_in?: boolean;
	user_id?: string;
	user_email?: string;
	property_id?: string;
	property_name?: string;
	property_county?: string;
	error?: string;
	enquiry_id?: string;
	environment?: 'local';
}

interface LoginAmplitudeEvent {
	user_email?: string;
	name?: string;
}

@Injectable({
	providedIn: 'root',
})
export class AmplitudeService {
	constructor(
		@Inject(PLATFORM_ID) private platformId: string,
		private readonly rendererFactory: RendererFactory2
	) {
		// Injecting DOM-related dependencies will not work on `APP_INITIALIZER` as DOM is not yet ready on this phase.
		// The solution is to create a renderer manually using the renderer factory.
		this.renderer = this.rendererFactory.createRenderer(null, null);
	}

	private renderer: Renderer2;

	public initializeAmplitude(): void {
		// Avoid loading the script multiple times or if not in the browser
		if (!environment.amplitudeId || window?.['amplitude']) return;

		const script = this.renderer.createElement('script');
		script.type = 'text/javascript';
		script.src = `https://cdn.amplitude.com/script/${environment.amplitudeId}.js`;

		// When the script loads, initialize Amplitude
		script.onload = () => {
			const amplitude = window?.['amplitude'];
			const sessionReplay = window?.['sessionReplay'];

			if (amplitude && sessionReplay) {
				amplitude.add(sessionReplay.plugin({ sampleRate: 1 }));
				amplitude.init(environment.amplitudeId, {
					fetchRemoteConfig: true,
					autocapture: true,
				});
			}
		};

		// Append the script to the document head
		this.renderer.appendChild(document.head, script);
	}

	updateUserId(email: string): void {
		if (isPlatformBrowser(this.platformId)) {
			if (window?.amplitude) {
				window.amplitude.setUserId(email);
			}
		}
	}

	handleSendEnquiryEvents(event: AmplitudeEvent, eventData: SendEnquiryAmplitudeEvent): void {
		if (isPlatformBrowser(this.platformId) && window.amplitude?.track) {
			window.amplitude.track(event, {
				...eventData,
				environment: environment.production ? 'production' : 'dev',
			});
		}
	}

	handleLoginEvents(event: AmplitudeEvent, eventData?: LoginAmplitudeEvent): void {
		if (isPlatformBrowser(this.platformId) && window.amplitude?.track) {
			window.amplitude.track(event, {
				...eventData,
				environment: environment.production ? 'production' : 'dev',
			});
		}
	}

	trackPlaceAdButtonClicked(text: string): void {
		if (isPlatformBrowser(this.platformId) && window.amplitude?.track) {
			window.amplitude.track(AmplitudeEvent.PLACE_AD_BUTTON_CLICKED, {
				text,
				environment: environment.production ? 'production' : 'dev',
			});
		}
	}
}
