import { Directive, HostListener, Inject, Input, PLATFORM_ID } from '@angular/core';
import { WindowService } from '../../core/services/window.service';
import { isPlatformBrowser } from '@angular/common';

@Directive({
    selector: '[externalLink]',
    standalone: false
})
export class ExternalLinkDirective {
	@Input('externalLink') urlString;

	@HostListener('click', ['$event'])
	confirm(el) {
		try {
			if (el.target.tagName === 'A') {
				const url = el.target.innerText;
				// let urlRegex = /(((https?:\/\/)|(www\.))[^\s]+\.[a-zA-Z]{2,3}(\/\S*)?)/g;
				const urlRegex =
					/((?:(http|https|Http|Https|rtsp|Rtsp):\/\/(?:(?:[a-zA-Z0-9\$\-\_\.\+\!\*\'\(\)\,\;\?\&\=]|(?:\%[a-fA-F0-9]{2})){1,64}(?:\:(?:[a-zA-Z0-9\$\-\_\.\+\!\*\'\(\)\,\;\?\&\=]|(?:\%[a-fA-F0-9]{2})){1,25})?\@)?)?((?:(?:[a-zA-Z0-9][a-zA-Z0-9\-]{0,64}\.)+(?:(?:aero|arpa|asia|a[cdefgilmnoqrstuwxz])|(?:biz|b[abdefghijmnorstvwyz])|(?:cat|com|coop|c[acdfghiklmnoruvxyz])|d[ejkmoz]|(?:edu|e[cegrstu])|f[ijkmor]|(?:gov|g[abdefghilmnpqrstuwy])|h[kmnrtu]|(?:info|int|i[delmnoqrst])|(?:jobs|j[emop])|k[eghimnrwyz]|l[abcikrstuvy]|(?:mil|mobi|museum|m[acdghklmnopqrstuvwxyz])|(?:name|net|n[acefgilopruz])|(?:org|om)|(?:pro|p[aefghklmnrstwy])|qa|r[eouw]|s[abcdeghijklmnortuvyz]|(?:tel|travel|t[cdfghjklmnoprtvwz])|u[agkmsyz]|v[aceginu]|w[fs]|y[etu]|z[amw]))|(?:(?:25[0-5]|2[0-4][0-9]|[0-1][0-9]{2}|[1-9][0-9]|[1-9])\.(?:25[0-5]|2[0-4][0-9]|[0-1][0-9]{2}|[1-9][0-9]|[1-9]|0)\.(?:25[0-5]|2[0-4][0-9]|[0-1][0-9]{2}|[1-9][0-9]|[1-9]|0)\.(?:25[0-5]|2[0-4][0-9]|[0-1][0-9]{2}|[1-9][0-9]|[0-9])))(?:\:\d{1,5})?)(\/(?:(?:[a-zA-Z0-9\;\/\?\:\@\&\=\#\~\-\.\+\!\*\'\(\)\,\_])|(?:\%[a-fA-F0-9]{2}))*)?(?:\b|$)/gi;
				const newUrl = url.replace(urlRegex, (url, b, c) => {
					return String(c) === 'www.' || !c ? 'http://' + url : url;
				});

				new URL(newUrl);
				if (isPlatformBrowser(this.platformId)) {
					this.window.reference.open(newUrl, '_blank').focus();
				}
			}
		} catch (e) {
			console.log(e);
		}
	}

	constructor(private window: WindowService, @Inject(PLATFORM_ID) private platformId: any) {}
}
