import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'adImagePipe',
    standalone: false
})
export class AdImagePipe implements PipeTransform {
	constructor() {}

	transform(image: string | ArrayBuffer): string | ArrayBuffer {
		return image ? image : 'assets/images/ad-not-available.svg';
	}
}
