import { Directive, Input, HostBinding } from '@angular/core';
import { DeviceDetectorService } from 'ngx-device-detector';

@Directive({
    selector: '[findqoAnchor]',
    standalone: false
})
export class AnchorDirective {
	constructor(private readonly deviceDetectorService: DeviceDetectorService) {}

	private _target: string;

	@HostBinding('attr.target')
	@Input()
	set target(value: string) {
		this._target = value;
		this.updateTarget();
	}

	get target(): string {
		return this._target;
	}

	/**
	 * Update the `target` attribute
	 *
	 * iPhone target `_blank` is not working when opening an external aop. The solution is to use '_self'.
	 */
	private updateTarget(): void {
		this._target = this.deviceDetectorService.isMobile() && this.deviceDetectorService.getDeviceInfo().device === 'iPhone' ? '_self' : this._target;
	}
}
