import { Pipe, PipeTransform } from '@angular/core';
import { FilterType } from '@core/enums';
import { RouteData } from '@core/services/route.service';

/**
 * Create the Ad list page heading
 *
 * TODO: Add translations
 */

@Pipe({
    name: 'adListPageHeading',
    standalone: false
})
export class AdListPageHeadingPipe implements PipeTransform {
	transform(routeData: RouteData): string {
		const AISLE = routeData?.aisle?.split('-').map((aisle: string) => this.toCapitalize(aisle));
		const STAND = routeData?.stand?.split('-').map((stand: string) => this.toCapitalize(stand));
		const BEDROOM_COUNT = routeData?.['pageData']?.[FilterType.BEDROOM_COUNT];
		let heading = '';

		if (STAND && BEDROOM_COUNT && ((BEDROOM_COUNT.length === 1 && BEDROOM_COUNT.includes('8')) || BEDROOM_COUNT === '8')) {
			heading = `Studio ${STAND.join(' ')} for ${this.toString(AISLE[2])} ${this.toString(AISLE[0])} in`;
		} else if (STAND && BEDROOM_COUNT && (BEDROOM_COUNT.length === 1 || typeof BEDROOM_COUNT === 'string')) {
			heading = `${BEDROOM_COUNT === '7' || BEDROOM_COUNT.includes('7') ? '7+' : BEDROOM_COUNT} Bedroom ${STAND.join(' ')} for ${this.toString(
				AISLE[2]
			)} ${this.toString(AISLE[0])} in`;
		} else if (STAND && routeData?.stand) {
			const FIRST_WORD = !!this.toString(AISLE[2]) ? ` ${this.toString(AISLE[2])}` : '';
			const SECOND_WORD = !!this.toString(AISLE[0]) ? ` ${this.toString(AISLE[0])}` : '';

			heading = `${STAND.join(' ')} for${FIRST_WORD}${SECOND_WORD} in`;
		} else if (AISLE) {
			const FIRST_WORD = !!this.toString(AISLE[2]) ? ` ${this.toString(AISLE[2])}` : '';
			const SECOND_WORD = !!this.toString(AISLE[0]) ? ` ${this.toString(AISLE[0])}` : '';

			const ADJECTIVE = this.toString(AISLE[1]);
			const NOUN = `${FIRST_WORD}${SECOND_WORD}`;

			heading = `${ADJECTIVE} Properties for${NOUN || ''} in`;
		}

		heading += ' Ireland';

		return heading;
	}

	private toCapitalize(value: string): string {
		return value.charAt(0).toLocaleUpperCase() + value.substring(1);
	}

	private toString(value: string): string {
		return !!value ? this.toCapitalize(value) : '';
	}
}
