import { Pipe, PipeTransform } from '@angular/core';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';

@Pipe({
    name: 'detailPropertyValue',
    standalone: false
})
export class DetailPropertyPipe implements PipeTransform {
	constructor(private placeAdFacade: PlaceAdFacade) {}

	transform(value: string): string {
		return this.placeAdFacade.getAdData()?.[value];
	}
}
