import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'maskPhone',
    standalone: false
})
export class MaskPhonePipe implements PipeTransform {
	transform(phone: string, visibleDigits = 5): string {
		if (!phone) {
			return '';
		}

		const visiblePart = phone.substring(0, visibleDigits);
		const maskedPart = '*'.repeat(Math.max(phone.length - visibleDigits, 0));

		return visiblePart + maskedPart;
	}
}
