import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'maskEmail',
    standalone: false
})
export class MaskEmailPipe implements PipeTransform {
	transform(email: string): string {
		if (!email) {
			return '';
		}

		let maskedUsername: string;
		const [USERNAME, DOMAIN] = email.split('@');
		const USERNAME_LENGTH = USERNAME.length;

		if (USERNAME_LENGTH <= 2) {
			maskedUsername = '*'.repeat(USERNAME_LENGTH);
		} else {
			maskedUsername = `${USERNAME.charAt(0)}${'*'.repeat(USERNAME_LENGTH - 1)}`;
		}

		const [DOMAIN_PREFIX, DOMAIN_SUFFIX] = DOMAIN.split('.');
		const MASKED_DOMAIN_PREFIX = `${DOMAIN_PREFIX.slice(0, 3)}${'*'.repeat(DOMAIN_PREFIX.length - 3)}`;
		const VISIBLE_DOMAIN_SUFFIX = DOMAIN_SUFFIX.slice(0, 3);
		const MASKED_DOMAIN = `${MASKED_DOMAIN_PREFIX}.${VISIBLE_DOMAIN_SUFFIX}`;

		return `${maskedUsername}@${MASKED_DOMAIN}`;
	}
}
