import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'adTransform',
    standalone: false
})
export class AdTransfromPipe implements PipeTransform {
	transform(value: any, property: string, yes?: string, no?: string, privateType?: string, business?: string): string {
		switch (property) {
			case 'isWarranty':
			case 'isOwnerOccupied':
			case 'isUnderConstruction':
				return value === '0' ? no : yes;

			case 'propertySize':
				if (value && typeof value === 'object') {
					return `${value.value ?? ''} ${value.parameter ?? ''}`.trim();
				}
				return '';

			case 'seller':
				if (value && typeof value === 'object') {
					return value.sellerTypeId === '1' ? privateType ?? '' : business ?? '';
				}
				return '';

			case 'availableFrom': {
				const date = new Date(+value * 1000);
				return new Intl.DateTimeFormat('en-US', {
					month: 'long',
					day: 'numeric',
					year: 'numeric',
					timeZone: 'UTC',
				}).format(date);
			}

			case 'taxDesignation':
				if (value && typeof value === 'object') {
					return value.name ?? '';
				}
				return '';

			default:
				return String(value ?? '');
		}
	}
}
