import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'maskMobile',
    standalone: false
})
export class MaskMobilePipe implements PipeTransform {
	transform(value: Object): string {
		if (!value) {
			return '';
		}

		const TOTAL_VALUE = value['prefix'] + value['number'];

		const VISIBLE_DIGITS = 3;
		const COUNTRY_CODE_LENGTH = value['prefix'].length;
		const MASKED_LENGTH = TOTAL_VALUE.length - VISIBLE_DIGITS - COUNTRY_CODE_LENGTH;
		const COUNTRY_CODE = TOTAL_VALUE.slice(0, COUNTRY_CODE_LENGTH);
		const MASKED = TOTAL_VALUE.slice(COUNTRY_CODE_LENGTH, COUNTRY_CODE_LENGTH + MASKED_LENGTH).replace(/\d/g, 'X');
		const VISIBLE = TOTAL_VALUE.slice(-VISIBLE_DIGITS);
		return `${COUNTRY_CODE}${MASKED}${VISIBLE}`;
	}
}
