import { Pipe, PipeTransform } from '@angular/core';
import { Ad } from '@core/models';

@Pipe({
    name: 'adTitle',
    standalone: false
})
export class AdTitlePipe implements PipeTransform {
	transform(ad: Ad): string {
		if (!ad) return '';

		const AISLE = ad?.aisle?.reference?.toLowerCase();

		if (AISLE?.includes('share')) {
			return ad?.ad_address?.trim() || '';
		}

		const BED_ROOM_COUNT = ad?.bedroomCount;
		const PROPERTY_TYPE = ad?.stand?.name;

		let typeLabel = '';
		if (AISLE?.includes('rent')) {
			typeLabel = 'for Rent';
		} else if (AISLE?.includes('sell')) {
			typeLabel = 'for Sale';
		}

		const TITLE_PARTS = [];

		if (Number(BED_ROOM_COUNT) > 0) {
			TITLE_PARTS.push(`${BED_ROOM_COUNT} Bedroom`);
		}

		if (PROPERTY_TYPE) {
			TITLE_PARTS.push(PROPERTY_TYPE);
		}

		if (typeLabel) {
			TITLE_PARTS.push(typeLabel);
		}

		let finalTitle = TITLE_PARTS.join(' ');

		if (ad?.countyArea && ad?.ad_address) {
			const COUNTY_AREA_SPLIT = ad?.countyArea.split(',');
			const COUNTY_AREA = COUNTY_AREA_SPLIT.slice(0, 2).join(',').trim();

			finalTitle += ` in ${COUNTY_AREA} - ${ad?.ad_address}`;
		}

		return finalTitle.trim() || '';
	}
}
