import { Pipe, PipeTransform } from '@angular/core';
import { Price } from '@core/models';

@Pipe({
	name: 'adPrice',
	standalone: false,
})
export class AdPricePipe implements PipeTransform {
	transform(price: Price, rentalPeriod?: string): string {
		if (price?.isPriceOnApplication === '1') {
			return 'Price on application';
		} else {
			const VALUE = price?.maxValue ? 'From ' + price?.symbol + price?.minValue + ' to ' + price?.symbol + price?.maxValue : price?.symbol + price?.minValue;

			if (rentalPeriod) {
				return VALUE + ' ' + rentalPeriod;
			}

			return VALUE;
		}
	}
}
