import { DOCUMENT } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { API, APP_ROUTE } from '@core/constants';
import { AmplitudeEvent, AuthType, PostLoginAction, StorageType } from '@core/enums';
import { AmplitudeService } from '@core/services/amplitude.service';
import { AppEmailValidator } from '@core/validators';
import { environment } from '@environments/environment';
import { AbstractAuthComponent } from '@feature/auth/abstracts/auth.abstract';
import { AuthFacade } from '@feature/auth/auth.facade';
import { Observable } from 'rxjs';

@Component({
    selector: 'findqo-login-v2',
    templateUrl: './login-v2.component.html',
    styleUrls: ['./login-v2.component.scss'],
    standalone: false
})
export class LoginV2Component extends AbstractAuthComponent implements OnInit {
	constructor(
		@Inject(DOCUMENT) private readonly document: Document,
		private readonly fb: FormBuilder,
		private readonly authFacade: AuthFacade,
		private readonly amplitudeService: AmplitudeService,
		private readonly router: Router
	) {
		super();
		this.isLoading$ = this.authFacade.isLoading$();
	}

	isLoading$: Observable<boolean>;
	form: FormGroup;

	readonly appRoute = APP_ROUTE;

	onRequestOtp(): void {
		if (this.form.invalid) {
			return;
		}

		const EMAIL = this.form.get('email').value;
		this.amplitudeService.handleLoginEvents(AmplitudeEvent.LOGIN_FORM_CONTINUE_CLICKED, { user_email: EMAIL });

		// Requet email OTP
		this.authFacade.requestEmailOtp(EMAIL);

		// Go to OTP screen
		this.changed.emit({
			authType: AuthType.OTP,
			email: EMAIL,
		});
	}

	private createForm(): void {
		this.form = this.fb.group({
			email: ['', [Validators.required, AppEmailValidator.validateEmailPattern()]],
		});
	}

	/**
	 * Redirects the user to Google SSO login.
	 *
	 * - Gets the URL the user was trying to visit (or uses current route).
	 * - Sets up where to redirect after login or error.
	 * - Stores a post-login action in sessionStorage.
	 * - Marks the login as user-initiated if a session exists.
	 * - Clears the stored attempted URL.
	 * - Navigates to the Google SSO redirect URL with parameters.
	 */
	onGoogleRedirect(): void {
		const attemptedUrl = sessionStorage.getItem(StorageType.AUTH_REDIRECT) || this.router.url;
		const baseUrl = environment.appUrl;

		const params = new URLSearchParams({
			successRedirectTo: `${baseUrl}/${attemptedUrl.replace(/^\/+/, '').replace(/[?&]error=access_denied(?=&|$)/, '')}`,
			errorRedirectTo: baseUrl,
		});

		const session = sessionStorage.getItem(StorageType.POST_LOGIN_SESSION);
		if (!session) {
			sessionStorage.setItem(StorageType.POST_LOGIN_SESSION, JSON.stringify({ type: PostLoginAction.NONE }));
		} else {
			sessionStorage.setItem(
				StorageType.POST_LOGIN_SESSION,
				JSON.stringify({
					...JSON.parse(session),
					userInitiatedSso: true, // This flag will be used if user
				})
			);
		}

		sessionStorage.removeItem(StorageType.AUTH_REDIRECT);

		this.document.location.href = `${API.auth.googleRedirect}?${params.toString()}`;
	}

	ngOnInit(): void {
		this.amplitudeService.handleLoginEvents(AmplitudeEvent.LOGIN_FORM_OPENED);
		this.createForm();
	}
}
