import { Component, EventEmitter, Input, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { AbstractFormControlValueAccessorComponent } from '../abstracts/form-control-value-accessor.abstract';

@Component({
    selector: 'findqo-input',
    templateUrl: './input.component.html',
    styleUrls: ['./input.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: InputComponent,
            multi: true,
        },
    ],
    standalone: false
})
export class InputComponent extends AbstractFormControlValueAccessorComponent {
	// TODO: Update the the emitter based on values we need to pass in parent component.
	// TODO: Check why `blur` event is not emtting

	@Input() type: 'text' | 'number' | 'email' | 'password' = 'text';
	@Input() placeholder = '';
	@Input() hint?: string;
	@Input() inputClass?: string;
	@Input() wrapperClass?: string;
	@Input() iconPrefix?: string;
	@Input() iconSuffix?: string;
	@Input() pattern?: string;
	@Input() isReadonly = false;
	@Input() maxLength = 524288;

	@Output() prefixClicked = new EventEmitter<void>();
	@Output() suffixClicked = new EventEmitter<void>();

	onPrefixClick(): void {
		this.prefixClicked.emit();
	}

	onSuffixClick(): void {
		this.suffixClicked.emit();
	}
}
