import { Directive, EventEmitter, HostListener, Output } from '@angular/core';

@Directive({
    selector: '[findqoHoverInOut]',
    standalone: false
})
export class HoverDirective {
	@Output() hoverStatus = new EventEmitter();

	@HostListener('mouseenter') onMouseEnter() {
		if (window?.['innerWidth'] <= 768) {
			// Do nothing for mobile devices (screens with width <= 768px)
			return;
		  }
		this.hoverStatus.emit({ hover: true });
	}

	@HostListener('mouseleave') onMouseLeave() {
		if (window?.['innerWidth'] <= 768) {
			// Do nothing for mobile devices (screens with width <= 768px)
			return;
		  }
		this.hoverStatus.emit({ hover: false });
	}
}

@Directive({
    selector: '[findqoHoverCardInOut]',
    standalone: false
})
export class HoverCardDirective {
	@Output() hoverStatus = new EventEmitter();

	@HostListener('mouseleave') onMouseLeave() {

		if (window?.['innerWidth'] <= 768) {
			// Do nothing for mobile devices (screens with width <= 768px)
			return;
		  }

		this.hoverStatus.emit({ hover: false });
	}
}
